"use client";
import { Monitor, ShoppingCart, BarChart4 } from "lucide-react";
import Link from "next/link";

export default function Services() {
  const services = [
    {
      title: "Performance Marketing",
      description:
        "ROI-driven ad campaigns on Google & Meta that scale ecommerce brands profitably. Focused on sales, not vanity metrics.",
      icon: <BarChart4 className="w-10 h-10 text-[#ebb2ff]" />,
    },
    {
      title: "Ecommerce Development",
      description:
        "Conversion-optimized Shopify & WordPress stores built to turn visitors into loyal customers.",
      icon: <ShoppingCart className="w-10 h-10 text-[#ebb2ff]" />,
    },
    {
      title: "SEO & Content Strategy",
      description:
        "Search-first strategies that bring consistent, high-intent traffic and measurable growth.",
      icon: <Monitor className="w-10 h-10 text-[#ebb2ff]" />,
    },
  ];

  return (
    <section className="max-w-6xl mx-auto px-8 py-20">
      {/* Heading */}
      <div className="text-center mb-16">
        <p>- Services</p>
        <div className="flex flex-2 gap-64">
            <h2 className="text-5xl font-bold mb-4">Services i Provide</h2>
            <a href="/services" className="btn-gradient rounded-lg">View All Services</a>
        </div>
        
        <p className="text-lg text-gray-400 max-w-2xl mx-auto">
          Helping ecommerce brands grow with performance marketing, optimized
          stores, and long-term traffic strategies.
        </p>
      </div>

      {/* Services Grid */}
      <div className="grid md:grid-cols-3 gap-10">
        {services.map((service, index) => (
          <div
            key={index}
            className="rounded-2xl bg-[#4241427d] backdrop-blur-md border border-white/20 
                       p-8 shadow-lg hover:shadow-2xl transition-all duration-300 hover:-translate-y-2"
          >
            <div className="mb-4">{service.icon}</div>
            <h3 className="text-2xl font-semibold mb-2">{service.title}</h3>
            <p className="text-gray-300">{service.description}</p>
          </div>
        ))}
      </div>

      {/* CTA */}
      <div className="text-center mt-16">
        <Link
          href="#contact"
          className="btn-gradient rounded-full px-8 py-3 text-lg font-semibold"
        >
          Let’s Work Together
        </Link>
      </div>
    </section>
  );
}
